import './App.css';
import React from 'react';
import Arkadiy from './images/Arkadiy.jpg'
import Dmitriy from './images/Dmitriy.jpg'
import Maxim from './images/Maxim.jpg'
import { useState } from "react";


function App() {

  const [developersList, setDevelopersList] = useState([
    {id: 1, name: 'Дмитрий', order: 1, source: Dmitriy},
    {id: 2, name: 'Аркадий', order: 2, source: Arkadiy},
    {id: 3, name: 'Максим', order: 3, source: Maxim},
  ])

  let currentDeveloper = null

  function dragStartHandler(e, develop) {
    currentDeveloper = develop
  }
  function dragEndHandler(e) {
    if (e.target.className === 'firstName') {
      e.target.parentNode.style.backgroundColor = 'transparent'
    }
    if (e.target.className === 'card') {
      e.target.style.backgroundColor = 'transparent'
    }
  }

  function dragOverHandler(e) {
    e.preventDefault()
    if (e.target.className === 'firstName') {
      e.target.parentNode.style.backgroundColor = 'rgba(128, 128, 128, 0.5)'
    }
    if (e.target.className === 'card') {
      e.target.style.backgroundColor = 'rgba(128, 128, 128, 0.5)'
    }
    
  }

  function dropHandler(e, develop) {
    e.preventDefault()
    console.log('drop', develop)
    setDevelopersList(developersList.map(c => {
      if (c.id === develop.id) {
        return {...c, order: currentDeveloper.order}
      }
      if (c.id === currentDeveloper.id) {
        return {...c, order: develop.order}
      }
      return c
    }))
  }

  const BuildCards = () => {
    return (
      <div className="cards">
        {developersList.map((develop) => (
          <div 
          
          onDragStart={e => dragStartHandler(e, develop)}
          onDragLeave={e => dragEndHandler(e)}
          onDragEnd={e => dragEndHandler(e)}
          onDragOver={e => dragOverHandler(e)}
          onDrop={e => dropHandler(e, develop)}
          draggable={true}
          key={develop.id}
          className={"card"} 
          name={develop.name} 
          style={{order: develop.order}}>
            <img src={develop.source} alt={develop.name}/> 
            <span className="firstName">{develop.name}</span>
          </div>)
        )}
      </div>
    )
  }





  return (
    <div className="App">
      <header className="App-header">
        <h1>Разработчики</h1>
      </header>
      <BuildCards />
    </div>
  );
}

export default App;
